
.headerAndLocation{
  height: 70px;
  width: 100%;
  top: 0;
  background: #000;
  color: #fff;
  display: flex;
  justify-content: space-around;
  align-items: center;
}


.footer{
  background: #000;
  height: 50px;
  width: 100%;
  position: absolute;
  bottom: 0px;
}

.selectLocation{
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  right: 20%;
}


.selectLocation ul{
  position: absolute;
  color:#444;
  list-style: none;
  top: 70px;
  margin: 0px 10px;
  padding: 0px;
  width: auto;
  box-sizing: border-box;
  box-shadow: 0px 0px 10px 5px #eee;
  min-width: 200px;
  text-transform: uppercase;
}


.selectLocation ul li{
    border-bottom: 1px solid #eee;
    display: block;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    box-sizing: border-box;
    background: #fff;
    z-index: 99;
    cursor: pointer;
}

.selectLocation:hover .showLocations{
  display: block;
}

.showLocations{
  display: none;
}

.sub-menu{
  display: none;
}

.eachLocation:hover .sub-menu{
  min-width: 150px;
  display: block;
  position: absolute;
  left: 100%;
  top: 0px;
  box-shadow: 0px 0px 10px 2px #eee;
}

.sub-menu section{
  display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    border-bottom: 1px solid #eee;
    background: #fff;
}

.eachLocation{
  position:relative;
}

.eachCategory {
  height: 200px;
  width: 300px;
  border: 1px solid #aaa;
  margin: 20px calc(4% - 14px);
}

.categoriesContainer{
    width: 100%;
    height: calc(100vh - 120px);
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    overflow-y: scroll;
    overflow-x: hidden;
    flex-direction: row;
}

.breadcrumb{
  height: 50px;
  width: 100%;
  margin-top: 20px;
  margin-left: calc(4% - 14px);
}

.breadcrumbElement{
  line-height: 50px;
}

.eachCategory .image-container{
  height: 80%;
}

.eachCategory .image-container img{
  width:100%;
  height: 100%;
}

.eachCategory .name-container{
  height: 20%;
  background: #009eff;
  display: flex;
  justify-content: center;
  align-items: center;
  color:white;
  position: relative;
  cursor: pointer;
}
.eachCategory  .name-container span{
  position: absolute;
  left:95%;
}
.eachCategory  .name-container .arrow {
  border: solid #fff;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;
}

.eachCategory  .name-container .right {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}

.homePageContainer{
  height: calc(100vh - 75px);
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.homePageContainer .heading{
  text-align: center;
  font-size: 1.5em;
  text-transform: uppercase;
}

.homePageContainer .heading .locationSelectorHelper{
  margin-top: 30px;
}

.breadcrumb span{
  cursor: pointer;
}

.selectLocation ul li:hover,.sub-menu section:hover{
  border-bottom: 1px solid #333;
}
